/* App.css */
body {
  margin: 0;
  padding: 0;
  background-image: url("https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/i/d0d99939-6e3f-48ed-a509-e0402aa359f5/d36xokb-d194f06f-bb36-4d10-a6d8-7bfa771a4225.jpg");
  background-size: cover;

  background-repeat: no-repeat;
  background-attachment: fixed;
  font-family: Arial, sans-serif;
}

/* Additional styles for different screen sizes */
@media (max-width: 768px) {
  body {
    background-size: contain; /* or another appropriate style for smaller screens */
  }
}

.App {
  text-align: center;
}

.App-header {
  background-color: rgba(40, 44, 52, 0.8);
  padding: 20px;
  color: white;
  position: relative;
}
button {
  margin: 10px;
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

.section {
  margin-top: 20px;
}

.Navbar {
  display: flex;
  justify-content: flex-end;
  padding: 10px;
  /* background-color: #333; */
}

.Navbar button {
  background-color: transparent;
  color: white;
  border: none;
  cursor: pointer;
}
